/* Section */
.section {
    clear: both;
    margin: 0px;
    padding: 0px;
  }
  /* Column */
  .col {
    background: #eee;
    display: block;
    float:left;
    margin: 1% 0 1% 1.6%;
  }
  .col:first-child { margin-left: 0 !important; }
  
  /* Row */
  .row:before,
  .row:after { content:""; display:table; }
  .row:after { clear:both;}
  .row { zoom:1; /* For IE 6/7 */ }
  
  /* Grid */
  .grid_1_of_3{ width: 32.267% }
  .grid_2_of_3{ width: 66.133% }
  .grid_3_of_3{ width: 100% }
  
  /* Full Width below 768 pixels */
  @media only screen and (max-width: 768px) {
    .col {  margin: 1% 0 1% 0%; }
    [class*='grid_'] { width: 100%; }
  }
.nav ul{
    background-color:#494e4d;
    padding: 3%;
    text-align: right;
}
.nav ul li{
    padding-top: 3%;
    display: inline-block;
    box-shadow: rgb(243, 239, 239);
    padding: 2%;
    color:white;
  
}
.nav a{
    color:rgb(242, 245, 248);
    text-decoration: none;
    font-size: 25px;
    
}
.nav a:hover{
    color:rgb(3, 54, 50);
    font-weight: bolder;
    transition: 3s;
}
